.burgerMenuIcon {
    margin-left: 70px;
    cursor: pointer;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1280px;
    height: 60px;
    margin-left: auto;
    margin-right: auto;

    background: #FFFFFF;
    box-shadow: 0px 0px 40px rgba(29, 33, 38, 0.13),
    0px 0px 2px rgba(29, 33, 38, 0.1);
}

.header h1 {
    margin-right: 70px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;

    color: #000000;
}