[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    background-color: var(--background);
    color: var(--color);
    margin-left: 70px;
    width: 171px;
    height: 36px;
    border-radius: 2px;
    border: 1px solid black;
    padding: 8px 10px;
}

.span {
    display: block;
    margin-left: 70px;
    margin-top: 65px;
    margin-bottom: 6px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: left;

}


