.sidebar {
    position: absolute;
    left: -300px;
}

.sidebar.open {
    display: block;
    position: absolute;

    z-index: 1;
    margin-left: 620px;

    width: 317px;
    height: 720px;

    background: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 0px;
}

.background {
    display: block;
    position: absolute;
    z-index: 1;
    margin-left: 320px;

    width: 1280px;
    height: 720px;
    background: #000000;
    opacity: 0.3;
}


