.loader {
    /*свг и/или анимация*/
    margin-top: 34px;
    margin-left: 70px;
    position: relative;
    width: 108px;
    height: 108px;
    border-radius: 50%;
    animation: ring 2s linear infinite;
    background: conic-gradient(from 90deg at 50% 50%, rgba(255, 255, 255, 0) 0deg, #512DE4 360deg);
}

.loader:before,
.loader:after {
    content: '';
    position: absolute;
    border-radius: 50%;
}

.loader:before {
    top: 10px;
    left: 10px;
    width: 88px;
    height: 88px;
    background: white;
    border-radius: 50%;
}

.loader:after {
    top: 50px;
    left: 98px;
    height: 10px;
    width: 10px;
    background: #512DE4;
}

@keyframes ring {
    from {
        transform: rotateZ(0);
    }
    to {
        transform: rotateZ(360deg);
    }
}