.buttonsContainer {
    padding-top: 47px;
    padding-left: 70px;
}

.clock {
    display: flex;
    flex-direction: column;
}

.watch {
    font-family: Montserrat, "sans-serif";
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: 35px;
    margin-left: 70px;
    width: 200px;
}

.watch strong {
    font-weight: 600;
}

.more {
    font-family: Montserrat, 'sans-serif';
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    padding-top: 8px;
    padding-left: 70px;
    color: #00000080;
}